import {getMyCoupons} from '@/http/product';
import loading from '@/components/loading2.vue'
export default {
	components: {
		loading
	},
	data(){
		return {
			beloading:false,
			pages:1,
			params:{
				"useStatus":0,
				"pageNum": 1,
				"pageSize": 10
			},
			couponList:[
				/* {
					name:'优惠券名称',
					useStatus:0,
					amount:50,
					require:200,
					validitType:1,
					validityStartDate:'2019.08.30',
					vaildityEndDate:'2019.09.30'
				} */
			]
		}
	},
	computed: {
	    isToken() {
	        return this.$store.state.login ? this.$store.state.login.token :''
	    }
	},
	watch:{
		isToken(){
			this.init();
		}
	},
	methods:{
		getMyCouponsFun(){
			this.beloading = true;
			//this.couponList = [];
			if(this.params.pageNum===1){
				this.couponList = [];
			}
			getMyCoupons(this.params).then(data=>{
				this.beloading = false;
				if(data.data.retCode===0){
					if(data.data.result){
						let couponList = data.data.result.list;
						this.couponList = [...this.couponList,...couponList]
						this.pages= data.data.result.pages;
					}
				}else{
					uni.showToast({
						title: data.data.retMsg,
						duration: 2000,
						icon:'none'
					});
				}
			}).catch(e=>{
				this.beloading = false;
				console.log('e',e)
				uni.showToast({
					title: e.data.retMsg,
					duration: 2000,
					icon:'none'
				});
			})
		},
		changeAct(val){
			this.params.pageNum =1;
			this.params.useStatus = val;
			
			this.getMyCouponsFun();
		},
		init(){
			if(this.isToken){
				this.getMyCouponsFun()
			}
		}
	},
	onLoad(){
		//this.init();
	},
	onShow(){
		this.init();
	},
	onReachBottom(){
		//加载下一页
		this.params.pageNum +=1;
		if(this.pages>=this.params.pageNum){
			this.getMyCouponsFun();
		}
	},
}
